.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

h1 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.cart {
  border: 2px solid #ddd;
  border-radius: 8px;
  padding: 20px;
}

.display{
  display: flex;
  justify-content: space-between;
}

.cart-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.cart-item div {
  flex: 1;
}

.cart-item h3 {
  font-size: 1.2rem;
  margin-bottom: 5px;
}

.cart-item p {
  margin: 5px 0;
  font-size: 1rem;
}

.cart-item button {
  padding: 8px 15px;
  margin-left: 5px;
  font-size: 1rem;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.cart-item button:hover {
  background-color: #0056b3;
}

.total {
  margin-top: 20px;
}
.right{
  display: flex;
  justify-content: space-between;
}
.right h3{
  background-color: #645cff;
  padding: 0.25rem 0.5rem;
  color: white;
  border-radius: 10px;
}
.total button {
  padding: 8px 15px;
  margin-left: 5px;
  font-size: 1rem;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.total h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.enclose button{
  background-color: white;
  color: #645cff;
}

.enclose button:hover{
  color: #282566;
  background-color: white;
}

